
/* Border Radius */

.btn, .jumbotron, .nav :hover {
    border-radius: 10px;
}

/* Product Image on Card */

.product-img {
    min-height: 100px;
    /* max-height: auto; */
}

/* Single Product Page - Product Name */

.name {
    background: black;
    color: #fff;
    font-weight: bold;
}


/* Title and description */

.title-desc {
    background-color: rgb(61, 180, 61);
}


/* Black Shade Form 10 - 1 */


.black-10 {
    background: #f2f2f2;
}

.black-9 {
    background: #e6e6e6;
}

.black-8 {
    background: #d9d9d9;
}



/* Cart Badge */

.cart-badge, .cart-badge:hover {

    border-radius: 50%;
    padding: 2px;
    font-size: 12px;
    font-style: italic;
    background: rgb(76, 124, 76);

}


